<template>
  <el-date-picker
    :disabled="disabled||config.disabled"
    v-model="config.value"
    :type="config.datetype"
    :placeholder="config.placeholder"
    :value-format="format"
  >
  </el-date-picker>
</template>

<script>
const dateFormat = {
  year: 'yyyy',
  month: 'yyyy-MM',
  week: 'yyyy 第 WW 周',
  date: 'yyyy-MM-dd',
  datetime: 'yyyy-MM-dd HH:mm:ss'
}
export default {
  name: 'DateField',
  props: {
    config: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    format () {
      return dateFormat[this.config.datetype]
    }
  }
}
</script>

<style scoped>
.el-date-editor{
  width: 100%;
}
</style>



