<template>
  <el-radio-group
    v-model="config.value"
    :disabled="disabled||config.disabled"
  >
    <el-radio v-for="(item,index) in config.data" :label="item.value" :key="index">{{item.text}}</el-radio>
  </el-radio-group>
</template>

<script>
export default {
  name: 'RadioField',
  props: {
    config: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.el-radio-group{
  width: 100%;
}
</style>



