<template>
  <el-input
    clearable
    :disabled="disabled||config.disabled"
    v-model="config.value"
    :placeholder="config.placeholder"
  >
  </el-input>
</template>

<script>
export default {
  name: 'TextField',
  props: {
    config: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
</style>



