<template>
  <el-switch
    :disabled="disabled||config.disabled"
    style="display: block"
    v-model="config.value">
  </el-switch>
</template>

<script>
export default {
  name: 'SwitchField',
  props: {
    config: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.el-switch{
  width: 100%;
}
</style>



