import Vue from 'vue'
import Router from 'vue-router'
import DiyFormList from '@/components/diy-form/DiyFormList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'DiyFormList',
      component: DiyFormList
    }
  ]
})



