<template>
  <el-container>
    <el-header>
      <el-input v-model="formName" placeholder="请输入表单名称或编码" ></el-input>
      <el-button @click="queryForm" type="primary" icon="el-icon-search" ></el-button>
      <el-button @click="addForm" type="primary" icon="el-icon-plus" ></el-button>
    </el-header>
    <el-main>
      <el-table
        v-loading="loading"
        :data="tableData">
        <el-table-column
          prop="formName"
          label="表单名称">
        </el-table-column>
        <el-table-column
          prop="formCode"
          label="表单编码">
        </el-table-column>
        <el-table-column
          prop="formVersion"
          label="版本">
        </el-table-column>
        <el-table-column
          prop="comments"
          label="备注">
        </el-table-column>
        <el-table-column
          align='center'
          fixed="right"
          label="操作"
          width="120">
          <template slot-scope="scope">
            <el-button
              @click="editForm(scope.$index, scope.row)"
              type="primary"
              icon="el-icon-edit"
              size="mini"
            >
            </el-button>
            <el-button
              @click="removeForm(scope.$index, scope.row)"
              type="danger"
              icon="el-icon-delete"
              size="mini"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        custom-class="fullscreen-dialog"
        fullscreen>
        <DiyForm ref="diyForm" :formId = "this.formId"></DiyForm>
      </el-dialog>
    </el-main>
    <el-footer>
    </el-footer>
  </el-container>
</template>

<script>
import axios from 'axios'
import DiyForm from './DiyForm.vue'
export default {
  name: 'DiyFormList',
  components: {
    DiyForm
  },
  mounted: function () {
    this.queryForm()
  },
  methods: {
    queryForm () {
      var me = this
      me.loading = true
      axios.get('/DiyFormController/getForm')
        .then(function (response) {
          me.loading = false
          me.tableData = response.data
        })
        .catch(function () {
          me.loading = false
        })
    },
    addForm () {
      this.editMode = false
      this.formId = null
      this.dialogVisible = true
      this.initDiyForm()
    },
    editForm (index, row) {
      this.editMode = true
      this.formId = row.formId
      this.dialogVisible = true
      this.initDiyForm()
    },
    initDiyForm () {
      if (this.$refs['diyForm']) {
        this.$refs['diyForm'].initForm(this.editMode, this.formId)
      }
    },
    removeForm (index, row) {
      var me = this
      me.$confirm('确认要删除该表单吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios.get('/DiyFormController/deleteForm', {
          params: {
            formId: row.formId
          }
        })
          .then(function (response) {
            me.$message({
              type: 'success',
              message: '操作成功！'
            })
            me.queryForm()
          })

        me.$message({
          type: 'success',
          message: '操作成功！'
        })
      })
    }
  },
  data () {
    return {
      loading: false,
      formName: '',
      dialogVisible: false,
      tableData: [],
      editMode: false,
      formId: null
    }
  }
}
</script>

<style scoped>
.el-table{
  width: 100%;
  height: 100%;
}
.el-header,.el-footer{
  height: auto !important;
  padding: 2px;
}
.el-main{
  padding: 0px;
}
.el-button{
  margin-left: 3px;
}
.el-input{
  width: 200px;
}
</style>



