<template>
  <el-date-picker
    :disabled="disabled||config.disabled"
    v-model="config.value"
    :type="config.daterange"
    range-separator="至"
    start-placeholder="开始"
    end-placeholder="结束"
    :value-format="format"
  >
  </el-date-picker>
</template>

<script>
const dateFormat = {
  daterange: 'yyyy-MM-dd',
  datetimerange: 'yyyy-MM-dd HH:mm:ss'
}
export default {
  name: 'DateRangeField',
  props: {
    config: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    format () {
      return dateFormat[this.config.daterange]
    }
  }
}
</script>

<style scoped>
.el-date-editor{
  width: 100%;
}
</style>



