<template>
  <el-input-number
    v-model="config.value"
    label="请输入内容"
    :min="config.min"
    :max="config.max"
    :precision="config.numberPrecision"
    :disabled="disabled||config.disabled"
  >
  </el-input-number>
</template>

<script>
export default {
  name: 'NumberField',
  props: {
    config: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.el-input-number{
  width: 100%;
}
</style>



