<template>
  <TextField v-if="config.type=='text'" :config="config" :disabled="disabled"></TextField>
  <TextAreaField v-else-if="config.type=='textarea'" :config="config" :disabled="disabled"></TextAreaField>
  <NumberField v-else-if="config.type=='number'" :config="config" :disabled="disabled"></NumberField>
  <DateField v-else-if="config.type=='date'" :config="config" :disabled="disabled"></DateField>
  <DateRangeField v-else-if="config.type=='daterange'" :config="config" :disabled="disabled"></DateRangeField>
  <RadioField v-else-if="config.type=='radio'" :config="config" :disabled="disabled"></RadioField>
  <CheckboxField v-else-if="config.type=='checkbox'" :config="config" :disabled="disabled"></CheckboxField>
  <SwitchField v-else-if="config.type=='switch'" :config="config" :disabled="disabled"></SwitchField>
  <SelectField v-else-if="config.type=='select'" :config="config" :disabled="disabled"></SelectField>
  <MutiSelectField v-else-if="config.type=='mutiselect'" :config="config" :disabled="disabled"></MutiSelectField>
  <div v-else>{{config.text}}</div>
</template>

<script>
import TextField from './form-field/TextField.vue'
import TextAreaField from './form-field/TextAreaField.vue'
import NumberField from './form-field/NumberField.vue'
import DateField from './form-field/DateField.vue'
import DateRangeField from './form-field/DateRangeField.vue'
import RadioField from './form-field/RadioField.vue'
import CheckboxField from './form-field/CheckboxField.vue'
import SwitchField from './form-field/SwitchField.vue'
import SelectField from './form-field/SelectField.vue'
import MutiSelectField from './form-field/MutiSelectField.vue'
export default {
  name: 'FormField',
  components: {
    TextField,
    TextAreaField,
    NumberField,
    DateField,
    DateRangeField,
    RadioField,
    CheckboxField,
    SwitchField,
    SelectField,
    MutiSelectField
  },
  props: {
    config: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
</style>



