<template>
  <el-select
    filterable
    clearable
    v-model="config.value"
    :placeholder="config.placeholder"
    :disabled="disabled||config.disabled"
  >
    <el-option
      v-for="(item,index) in config.data"
      :key="index"
      :label="item.text"
      :value="item.value">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: 'SelectField',
  props: {
    config: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.el-select{
  width: 100%;
}
</style>



